<template>
  <div class="allGroup">
    <bar></bar>
    <v-container>
      <v-card>
        <v-card-title>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-card-title>
        <v-row justify="center">
          <v-chip-group active-class="primary--text">
            <v-chip v-for="(tag, i) in tags" :key="i" @click="search = tag.Tag">
              {{ tag.Tag }}
            </v-chip>
          </v-chip-group>
        </v-row>
        <v-data-table :headers="headers" :items="groups" :search="search">
          <template v-slot:item.action="{ item }">
          <v-btn text color="cyan" :to="'/Group/GroupIndex/' + item.groupID">  </v-btn>
        </template>
        </v-data-table>
        
      </v-card>
    </v-container>
  </div>
</template>

<script>
import bar from "../components/Bar.vue";
import { mockGroups } from "@/mock/index.js";

export default {
  data() {
    return {
      search: "",
      choosedTag: "",
      tags: [
        {
          Tag: "",
        },
        {
          Tag: "",
        },
        {
          Tag: "",
        },
        {
          Tag: "",
        },
        {
          Tag: "",
        },
        {
          Tag: "",
        },
        {
          Tag: "",
        },
        {
          Tag: "",
        },
        {
          Tag: "",
        },
        {
          Tag: "IT",
        },
      ],
      groups: [],
      headers: [
        {
          text: "",
          align: "start",
          value: "groupName",
        },
        { text: "ID", value: "groupID" },
        { text: "", value: "tag" },
        { text: "", value: "memberNum" },
        { text: "", value: "introduce" },
        { text: "ID", value: "createID" },
        { text: "", value: "action" },
      ],
    };
  },
  components: {
    bar,
  },
  created() {
    this.searchGroup();
  },
  methods: {
    useMockData() {
      this.groups = mockGroups.map(group => ({
        groupID: group.id,
        groupName: group.name,
        tag: group.description,
        memberNum: group.memberCount,
        introduce: group.description,
        createID: 1 // mockID
      }));
    },
    
    searchGroup() {
      this.$http({
        method: "get",
        url: "/searchGroup",
      })
        .then((res) => {
          console.log(res.data);
          if (res.data.success && res.data.group && res.data.group.length > 0) {
            this.groups = res.data.group;
          } else {
            // mock
            this.useMockData();
          }
        })
        .catch((e) => {
          console.log(e);
          this.useMockData();
        });
    },
  },
};
</script>
