<template>
  <div class="SearchBook">
    <bar></bar>
    <v-container>
      <v-card>
        <v-card-title>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-card-title>
        <v-row justify="center">
          <v-chip-group active-class="primary--text">
            <v-chip
              v-for="(tag,i) in tags"
              :key="i"
              @click="search = tag.Tag"
            >
              {{ tag.Tag }}
            </v-chip>
          </v-chip-group>
        </v-row>
        <v-row class="searchText" justify="center"></v-row>
        <v-data-table
          :headers="headers"
          :items="book"
          :search="search"
        >
        <template v-slot:item.action="{ item }">
          <v-btn text color="cyan" :to="'/Book/CheckBook/' + item.bookID">  </v-btn>
        </template>
        </v-data-table>
      </v-card>
    </v-container>
  </div>
</template>

<script>
import bar from "../components/Bar.vue";
import { mockBooks } from "@/mock/index.js";

export default {
  data() {
    return {
      search: "",
      choosedTag:"",
      tags: [
        {
          Tag: "",
        },
        {
          Tag: "",
        },
        {
          Tag: "",
        },
        {
          Tag: "",
        },
        {
          Tag: "",
        },
        {
          Tag: "",
        },
        {
          Tag: "",
        },
        {
          Tag: "",
        },
        {
          Tag: "",
        },
        {
          Tag: "IT",
        },
      ],
      headers: [
        {
          text: "",
          align: "start",
          filterable: false,
          value: "bookName",
        },
        { text: "", value: "kind" },
        { text: "", value: "author" },
        { text: "", value: "score" },
        { text: "", value: "action" },
      ],
      book: [],
    };
  },
  components: {
    bar,
  },
  created() {
    this.searchBook();
  },
  methods: {
    useMockData() {
      this.book = mockBooks.map(book => ({
        bookID: book.id,
        bookName: book.title,
        kind: book.category,
        author: book.author,
        score: book.rating
      }));
    },
    
    searchBook() {
      this.$http({
        method: "get",
        url: "/searchBook",
      })
        .then((res) => {
          if (res.data.success && res.data.book && res.data.book.length > 0) {
            console.log(res.data)
            this.book = res.data.book;
          } else {
            // mock
            this.useMockData();
          }
        })
        .catch((e) => {
          console.log(e);
          this.useMockData();
        });
    },
  },
};
</script>
